import { FormManager } from '../Common/FormManager';
import { onDocReady } from '../Common/UiHelper';
import { Modal } from 'bootstrap';

export class RegisterVenueSubmitManager {

    async Init(): Promise<void> {
        let backButton = document.getElementById("submitVenueRegistrationPrevButton") as HTMLButtonElement;
        if (backButton !== null) {
            backButton.addEventListener("click", (e: Event) => this.Save(2));
        }

        let nextButton = document.getElementById("submitVenueRegistrationButton") as HTMLButtonElement;
        if (nextButton !== null) {
            nextButton.addEventListener("click", (e: Event) => this.Save(4));
        }

        let step1Tab = document.getElementById("step1Tab") as HTMLLIElement;
        if (step1Tab !== null) {
            step1Tab.addEventListener("click", (e: Event) => this.Save(1));
        }

        let step2Tab = document.getElementById("step2Tab") as HTMLLIElement;
        if (step2Tab !== null) {
            step2Tab.addEventListener("click", (e: Event) => this.Save(2));
        }

        let printLicense = document.getElementById("PrintLink") as HTMLLinkElement;
        if (printLicense !== null) {
            printLicense.addEventListener("click", (e: Event) => this.PrintTextArea());
        }
    }

    async Save(step: number): Promise<void> {
        if (step == 1 || step == 2) {
            let host = document.getElementById("HostName") as HTMLInputElement;
            if (host != null) {
                let spaceHidden = document.getElementById("VenueRegisterView_SpaceId") as HTMLButtonElement;
                let roomHidden = document.getElementById("VenueRegisterView_RoomId") as HTMLButtonElement;
                window.location.href = `${host.value}/meeting-space/register/${step}/${spaceHidden.value}/${roomHidden.value}`;
            }
            return;
        }
        let formManager = new FormManager('venueConfirmationForm');
        if (await formManager.ValidateForm()) {
            let response = await formManager.SubmitForm();
            if (response.ok) {
                var confirmModel = document.getElementById('staticBackdrop')
                var modal = new Modal(confirmModel)
                modal.show();
            } else {
                await formManager.ShowSuccessFailureMessage(response);
            }
        }
    }

    async PrintTextArea(): Promise<void> {
        let textArea = document.getElementById('AgreementTextArea') as HTMLTextAreaElement
        let childWindow = window.open('', 'childWindow', 'location=yes, menubar=yes, toolbar=yes');
        childWindow.document.open();
        childWindow.document.write('<html><head></head><body>');
        childWindow.document.write(textArea.value.replace(/\n/gi, '<br>'));
        childWindow.document.write('</body></html>');
        childWindow.print();
        childWindow.document.close();
        childWindow.close();
    }
}
onDocReady(async () => {
    let page = new RegisterVenueSubmitManager();
    await page.Init();
});